import { Box, ThemeProvider } from '@material-ui/core'
import MusicPlayer from 'component/music-player'
import GamePage from 'page/game-page'
import LandingPage from 'page/landing-page'
import StoryPage from 'page/story-page'
import React from 'react'
import { BrowserRouter, Route } from 'react-router-dom'
import { theme } from 'theme/theme'

export const LANDING_PAGE = '/'
export const GAME_PAGE = '/game'
export const STORY_PAGE = '/story'

function App() {
	return (
		<ThemeProvider theme={theme}>
			<Box className='App' position='relative'>
				<Box position='absolute' top='15px' right='15px' zIndex={100}>
					<MusicPlayer defaultToggle={true} />
				</Box>

				<BrowserRouter>
					<Route path={LANDING_PAGE} exact>
						<LandingPage />
					</Route>
					<Route path={GAME_PAGE}>
						<GamePage />
					</Route>
					<Route path={STORY_PAGE}>
						<StoryPage />
					</Route>
				</BrowserRouter>
			</Box>
		</ThemeProvider>
	)
}

export default App
