import { Box, Snackbar, Typography } from '@material-ui/core'
import Button from 'component/button'
import { useBoolState } from 'page/game-page/hooks/use-bool'
import React from 'react'
import { copyToClipboard } from 'utils/clipboard'
import CardContainer from './card-container'

export type PropagandaCardProps = { openShareDialog: (...args: any[]) => any }
const PropagandaCard = (p: PropagandaCardProps) => {
	const [msgBarOpenState, openMsgBar, closeMsgBar] = useBoolState(false)

	const onShareButtonClick = () => {
		copyToClipboard(window.location.host)
		openMsgBar()
	}
	return (
		<>
			<CardContainer>
				<Box mt={12} display='flex' alignItems='center' flexDirection='column'>
					<Box width={180}>
						<Typography align='center' variant='h5'>
							对心智障碍人群 多一点关心
						</Typography>
					</Box>
					<Box mt={5} />
					<Box width={260}>
						<Typography variant='body2' align='center'>
							多了解一点这个群体的故事
						</Typography>
						<Typography variant='body2' align='center'>
							多一点理解、支持和包容
						</Typography>
					</Box>
				</Box>
				<Box display='flex' flexDirection='column' alignItems='center' mb={6} maxWidth='80%'>
					<Button variant='contained' color='primary' onClick={onShareButtonClick}>
						让更多人看见
					</Button>
					<Box mt={2} />
					<Button variant='contained' color='secondary' onClick={p.openShareDialog}>
						了解阳光家园
					</Button>
				</Box>
			</CardContainer>
			<Snackbar
				open={msgBarOpenState}
				onClose={closeMsgBar}
				autoHideDuration={1000}
				message='链接已复制'
				anchorOrigin={{
					vertical: 'bottom',
					horizontal: 'center'
				}}
			/>
		</>
	)
}
export default PropagandaCard
