import { Box } from '@material-ui/core'
import MusicNoteIcon from '@material-ui/icons/MusicNote'
import MusicOffIcon from '@material-ui/icons/MusicOff'
import { useBoolState } from 'page/game-page/hooks/use-bool'
import React from 'react'
import { Audios } from 'utils/cos-resource'

export type MusicPlayerProps = {
	defaultToggle: boolean
}
const MusicPlayer = (p: MusicPlayerProps) => {
	const [musicOn, turnOnMusic, turnOffMusic] = useBoolState(p.defaultToggle)

	const toggleMusic = (on: boolean) => {
		const audio = document.querySelector('audio')
		if (audio) {
			audio.volume = 0.2
			on ? audio.play() : audio.pause()
		}
	}
	const onClick = () => {
		musicOn ? turnOffMusic() : turnOnMusic()
		toggleMusic(!musicOn)
	}

	return (
		<Box onClick={onClick}>
			{musicOn ? <MusicNoteIcon color='secondary' /> : <MusicOffIcon color='secondary' />}
			<audio src={Audios.bgm} loop preload='auto' autoPlay />
		</Box>
	)
}
export default MusicPlayer
